  * {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  body {
    font-family: 'Lato', 'Helvetica Neue', sans-serif;
    background-color: #F4F4F7;
  }

  h1 {
    font-family: 'Anton', sans-serif;
  }

  a.link-unstyled {
    color: inherit;
  }

  a.link-unstyled:hover {
    color: inherit;
    text-decoration: none;
  }

  .btn {
    padding: 0.375rem 2rem;
  }

  .btn.btn-primary {
    color: #fff;
    background-color: #7DCD40;
    border-color: #7DCD40;
  }

  .btn.btn-primary:not(:disabled):not(.disabled):hover {
    color: #fff;
    background-color: #7DCD40;
    border-color: #7DCD40;
  }

  .btn.btn-primary:not(:disabled):not(.disabled):active {
    color: #fff;
    background-color: #7DCD40;
    border-color: #7DCD40;
  }

  .btn.btn-primary:not(:disabled):not(.disabled):focus {
    box-shadow: 0 0 0 0.2rem rgba(125, 205, 64, 0.5);
  }

  .btn.btn-danger {
    color: #fff;
    background-color: #CD4040;
    border-color: #CD4040;
  }

  .btn.btn-danger:not(:disabled):not(.disabled):hover {
    color: #fff;
    background-color: #CD4040;
    border-color: #CD4040;
  }

  .btn.btn-danger:not(:disabled):not(.disabled):active {
    color: #fff;
    background-color: #CD4040;
    border-color: #CD4040;
  }

  .btn.btn-danger:not(:disabled):not(.disabled):focus {
    box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
  }

  .Badge {
  background: #FFFFFF;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.10);
  border-radius: 8px 8px 8px 8px;
  overflow: hidden;
  height: 380px;
  }

  .Badge__header {
  padding: 0.5rem 0;
  height: 80px;
  background: #1B1B25;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  }

  .Badge__section-name {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 1rem 0;
  }

  .Badge__section-info {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 0.5rem 0;
  background: #F4F4F7;
  }

  .Badge__avatar {
  border-radius: 50%;
  margin-right: 1rem;
  width: 120px;
  height: 120px;
  }

  .Badge__footer {
  height: 54px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  color: #98CA3F;
  font-weight: bold;
  font-style: italic;
  }

.Badges__hero {
    width: 100%;
    padding: 2rem 0;
    background: url(../../static/media/stars.1d17360a.svg), #1B1B25;
    background-repeat: repeat;
    margin-bottom: 1rem;
  }

  .Badges__hero>.Badges__container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
